  getUniswapPrice = () => {
    axios.post("https://api.thegraph.com/subgraphs/name/uniswap/uniswap-v2", {
      query: `
      {
        bundle(id: "1") {
          ethPrice
        }
      }
      `
    })
      .then((res) => {
        // console.log(res.request.response.substring(31, 40));
        document.querySelector(".ETHprice").innerHTML = "$" + res.request.response.substring(31, 40);
      })
      .catch((err) => {
        console.log(err);
      })
  };


