  getYourDeposit = async () => {
    const { accounts, contract, rinkebyNFT } = this.state;

    let _mortgageId = this.mortgageId1.current.value;
    // let _mortgageId = mortgageId;

    document.querySelector(".getYourDeposit").innerHTML = "Querying...";

    if (this.state.networkId === 4 || this.state.networkId === "0x4" || this.state.networkId === 212984383488152) {
      try {
        await contract.methods.getDepositDetails(_mortgageId).call({
          from: accounts[0],
          to: contract._address
        }).then(async function (mortgage) {
          console.log(mortgage);

          if (mortgage[0] === "0x0000000000000000000000000000000000000000") {
            document.querySelector(".getYourDeposit").innerHTML = "The deposit does not exist, please enter the correct deposit ID!";
            return;
          } else {
            document.querySelector(".getYourDeposit").innerHTML = "The results are as follows:";
            document.querySelector(".mJia").innerHTML = mortgage[0];
            document.querySelector(".mYi").innerHTML = mortgage[1];
            document.querySelector(".mBing1").innerHTML = mortgage[2][0];

            if (mortgage[2].length === 3) {
              //xianshi
              document.querySelector(".Judge2").style.display = "block";
              document.querySelector(".Judge3").style.display = "block";


              document.querySelector(".mBing2").innerHTML = mortgage[2][1];
              document.querySelector(".mBing3").innerHTML = mortgage[2][2];
            }
            else if (mortgage[2].length === 5) {
              //xianshi
              document.querySelector(".Judge2").style.display = "block";
              document.querySelector(".Judge3").style.display = "block";
              document.querySelector(".Judge4").style.display = "block";
              document.querySelector(".Judge5").style.display = "block";


              document.querySelector(".mBing2").innerHTML = mortgage[2][1];
              document.querySelector(".mBing3").innerHTML = mortgage[2][2];
              document.querySelector(".mBing4").innerHTML = mortgage[2][3];
              document.querySelector(".mBing5").innerHTML = mortgage[2][4];
            }
            else if (mortgage[2].length === 7) {
              //xianshi
              document.querySelector(".Judge2").style.display = "block";
              document.querySelector(".Judge3").style.display = "block";
              document.querySelector(".Judge4").style.display = "block";
              document.querySelector(".Judge5").style.display = "block";
              document.querySelector(".Judge6").style.display = "block";
              document.querySelector(".Judge7").style.display = "block";


              document.querySelector(".mBing2").innerHTML = mortgage[2][1];
              document.querySelector(".mBing3").innerHTML = mortgage[2][2];
              document.querySelector(".mBing4").innerHTML = mortgage[2][3];
              document.querySelector(".mBing5").innerHTML = mortgage[2][4];
              document.querySelector(".mBing6").innerHTML = mortgage[2][5];
              document.querySelector(".mBing7").innerHTML = mortgage[2][6];
            }

            await contract.methods.getDepositTokenDetails(_mortgageId).call({
              from: accounts[0]
            }).then(function (result) {
              if (result[2] === "ETH") {
                document.querySelector(".mYajin").innerHTML = mortgage[3] / 1000000000000000000 + " " + "ETH";
              }
              else if (result[2] === "Wrapped Ether") {
                document.querySelector(".mYajin").innerHTML = mortgage[3] / 1000000000000000000 + " " + "wETH";
              }
            })




            //rollup链区块产生速度不同，需要更改锁定时间的计算方式
            document.querySelector(".mTime").innerHTML = mortgage[4] / 5760 + " " + "days";
          };



          // if (mortgage[4] === "0" && mortgage[3] === "0") {
          //   document.querySelector(".mRemainedTime").innerHTML = "0";
          //   document.querySelector(".mYajin").innerHTML = "0";
          // };

          //剩余时间
          await contract.methods.getRemainedTime(_mortgageId).call({
            from: accounts[0],
            to: contract._address
          }).then(function (result) {
            // console.log(result);
            if (result < "8640" && result > "0") {
              document.querySelector(".mRemainedTime").innerHTML = "less than one day, the deposit can be redeemed if " + result + " block is generated ";
            } else if (result === "0") {
              document.querySelector(".mRemainedTime").innerHTML = "Time is up!";
            } else {
              document.querySelector(".mRemainedTime").innerHTML = (result / 8640).toFixed(6) + "days, the deposit can be redeemed if" + " " + result + " " + "block is generated";
            };
          })


          //押金是否退了
          if (mortgage[3] === "0") {
            document.querySelector(".mYajin").innerHTML = "Deposit refunded";
            document.querySelector(".mRemainedTime").innerHTML = "Deposit refunded";
          };


        });

        await contract.methods.whetherToAdvance(_mortgageId).call({
          from: accounts[0],
          to: contract._address
        }).then(function (result) {
          console.log(result);
          document.querySelector(".notQuarter").innerHTML = result[0];
          document.querySelector(".notHalf").innerHTML = result[1];
        });

        await rinkebyNFT.methods.isOwner(_mortgageId).call({
          from: accounts[0]
        }).then(function (result) {
          console.log(result);

          document.querySelector(".NFTowner").innerHTML = result[1];
        });

      } catch (error) {
        console.log(error);
        document.querySelector(".getYourDeposit").innerHTML = "Please enter the correct deposit Id and check your network";
      }
    }
    else {
      document.querySelector(".getYourDeposit").innerHTML = "Please check your network";
    }







  };

