  bingIntervention = async () => {
    const { accounts, contract, rinkebyNFT } = this.state;

    let _mortgageId = this.mortgageId8.current.value;
    let _to = this.to1.current.value; //钱要判给谁就写谁的地址，只能是甲、乙或NFT拥有者
    var _isJia;
    var _isYi;
    var _isOwner;

    if (_mortgageId === "" || _to === "") {
      document.querySelector(".bingIntervention").innerHTML = "Please enter the correct wallet address and deposit ID!";
    } else {
      document.querySelector(".bingIntervention").innerHTML = "Checking the supported wallet address...";

      try {
        await contract.methods.getDepositDetails(_mortgageId).call({
          from: accounts[0]
        }).then(function (result) {
          _isJia = result[0];
          _isYi = result[1];
        });

        await rinkebyNFT.methods.isOwner(_mortgageId).call({
          from: accounts[0]
        }).then(function (result) {
          _isOwner = result[1];
        });
      } catch (error) {
        console.log(error);
        document.querySelector(".bingIntervention").innerHTML = "Please enter the correct wallet address and deposit ID!";
        return;
      }


      if (this.state.networkId === 4 || this.state.networkId === "0x4" || this.state.networkId === 212984383488152) {


        if (_to !== _isJia && _to !== _isYi && _to !== _isOwner) {
          document.querySelector(".bingIntervention").innerHTML = "Please enter the correct wallet address!";

          return;
        } else {
          document.querySelector(".bingIntervention").innerHTML = "Voting...";

          try {
            await contract.methods.bingIntervention(_to, _mortgageId).send({
              from: accounts[0],
              to: contract._address
            });

            document.querySelector(".bingIntervention").innerHTML = "Vote successful!";

            await contract.events.BingIntervention({
              fromBlock: 8813989
            }, function (error, event) {
              console.log(error);
              console.log(event);

              document.querySelector(".bingIntervention").innerHTML = ("Vote successful! The dispute is created successfully, the ID is" + event.returnValues.mortgageId);
            });

            await contract.events.TransferFunds({
              fromBlock: 8813989
            }, function (error, event) {
              console.log(error);
              console.log(event);

              document.querySelector(".signTransaction").innerHTML = "The voting is over! The deposit has been refunded to" + event.returnValues.to + ",the refund amount is" + event.returnValues.amount + "!";
            });


          } catch (error) {
            console.log(error);
            if (error.message === "MetaMask Tx Signature: User denied transaction signature.") {
              document.querySelector(".bingIntervention").innerHTML = "User cancelled!";
            } else if (error.code === -32602) {
              document.querySelector(".bingIntervention").innerHTML = "Please refresh the page and try again!";
              window.alert("Please refresh the page and try again!");
            } else {
              document.querySelector(".bingIntervention").innerHTML = "Please enter the correct wallet address and deposit ID and check your network!"
            };
          };


        }

      }
      else {
        document.querySelector(".bingIntervention").innerHTML = "Please check your network"
      }
    }



  };

